.page {
  background: url('../../static/image/share/bg1.png') repeat-x;
}

.share {
  width: 100%;
  background: url('../../static/image/share/bg.png') repeat-x;
  background-size: contain;
}

.headimg {
  width: 100%;
  position: relative;
  z-index: 30;
}

.downSrc {
  width: 7.386667rem;
  height: 1.013333rem;
  padding: 0 0.266667rem;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1.013333rem;
  background-color: #5d9ffd;
  color: white;
  font-weight: bold;
  font-size: 0.48rem;
  position: absolute;
  bottom: 0.72rem;
  left: 50%;
  margin-left: -3.693333rem;
  border-radius: 0.186667rem;
  box-shadow: 0.2rem 0.106667rem #2361b8;
}

.downSrc a {
  color: white;
}

.user {
  z-index: 11;
  margin: 1.333333rem 0.533333rem 0;
  height: 4.64rem;
  position: relative;
}

.user-headimg {
  width: 2.666667rem;
  background-size: contain;
  margin: 0.426667rem 0.533333rem 0 0.266667rem;
  position: relative;
}

.user-head {
  border-radius: 50%;
  overflow: hidden;
  width: 2.08rem;
  height: 2.08rem;
  left: 50%;
  margin-left: -1.04rem;
  margin-top: -0.48rem;
  position: absolute;
  background: url('../../static/image/share/userHeadBg.png') center no-repeat;
  background-size: cover;
  padding: 0.08rem;
}

.user-head img {
  border-radius: 50%;
  display: block;
}

.user-info {
  padding: 0.746667rem 0;
  color: #1d5eb2;
  font-weight: bold;
  font-size: 0.586667rem;
  line-height: 1.066667rem;
  text-align: left;
}

.shadow {
  background-color: #346ac4;
  transform: translate(0, 0.053333rem) rotate(-4deg);
  position: absolute;
  width: 102%;
  height: 100%;
  margin-left: -0.053333rem;
  margin-top: 0.053333rem;
  border-radius: 0.666667rem;
}

.shadow-body {
  background-color: #fff;
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 0.666667rem;
  border: 0.133333rem solid #509afb;
  display: -ms-flexbox;
  display: flex;
  z-index: 1;
}

.diao {
  top: -2.533333rem;
  left: 50%;
  transform: translate(-0.16rem, 0);
  position: absolute;
  width: 0.32rem;
  height: 2.666667rem;
  z-index: 2;
  margin: 0 auto;
}

.diaoBottom {
  bottom: -1.2rem;
  top: auto;
  z-index: 1;
}

.diao .gang {
  height: 100%;
  width: 0.106667rem;
  margin: 0 auto;
  background-color: #1e5eb6;
}

.diao .yuan {
  position: absolute;
  width: 0.32rem;
  height: 0.32rem;
  border-radius: 50%;
  background-color: #1e5eb6;
}

.good {
  margin: 0.8rem 0;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  z-index: 6;
}

.good-big {
  width: 4.586667rem;
  height: 4.426667rem;
  position: relative;
}

.good-small {
  width: 3.066667rem;
  height: 2.666667rem;
  position: relative;
  margin: 1.76rem 0 0 0.533333rem;
}

.zhanji {
  margin: 0 0.4rem 0.8rem;
  position: relative;
  color: #1d5eb2;
  font-weight: bold;
  font-size: 0.586667rem;
  line-height: 0.933333rem;
}

.zhanji .title {
  background: url('../../static/image/share/zhanji.png') center no-repeat;
  width: 5.12rem;
  height: 1.04rem;
  background-size: contain;
  margin: 0.746667rem auto 0;
}

.zhanji .shadow-body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  padding: 0 0 0.266667rem;
}

.getTimes {
  border: 0.053333rem dashed  #59abfd;
  border-radius: 0.266667rem;
  margin: 0.32rem 0.853333rem;
  padding: 0.133333rem 0;
}


